import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { LimitBound } from '../../models/limits-bound-types.model';

@Component({
  selector: 'bb-limit-bound-item',
  templateUrl: 'limit-bound-item.component.html',
})
export class LimitBoundItemComponent {
  /**
   * Title for limit bound item.
   */
  @Input() limitBound!: LimitBound;
  /**
   * Currency value.
   */
  @Input() currency!: string;
  /**
   * Flag for show/hide regular limit row.
   */
  @Input() showShadowLimit = false;
  /**
   * Flag for show/hide shadow limit column.
   */
  @Input() showRegularLimit = false;
}

@Directive({
  selector: '[bbLimitBoundItemCustomizable]',
})
export class LimitBoundItemCustomizableDirective extends BbTemplate<LimitBoundItemComponent, void> {}
